import styled from 'styled-components';

const Price = styled.p`
    font-family: 'Arial', Helvetica, sans-serif;
    font-weight: bold;
    font-size: 30px;
    margin: 0 0 40px;

`;
export const PriceText = styled.span`
    display: inline-block;
    font-weight: normal;
    font-size: 12px;
    color: #666;
    padding-left: 5px;
`;

export default Price


